<template>
  <div class="detail_cont">
    <div class="detail_mag02">
      <div class="ball"></div>

      <section class="section1" @mousemove="bubbleMove($event)">
        <div class="bubble-area">
          <i class="bubble bubble-01"></i>
          <i class="bubble bubble-02"></i>
          <i class="bubble bubble-03"></i>
          <i class="bubble bubble-04"></i>
          <i class="bubble bubble-05"></i>
          <i class="bubble bubble-06"></i>
          <i class="bubble bubble-07"></i>
        </div>
        <div class="inner">
          <div class="content text-box">
            <h2 class="sc-title">#챌린지 도전하기🌳</h2>
            <span>누구나 참여 가능한 GREENY의 아나바다 챌린지에 도전해 보세요!</span>
            <span>그리니의 아나바다 챌린지는</span>
            <span>아껴쓰고, 나눠쓰고, 바꿔쓰고, 다시쓰는</span>
            <span>'아나바다운동'을 모티브로 슬로우패션을 추구하는 챌린지입니다</span>
            <span>그럼 그리니의 아나바다 챌린지의 예시를 살펴볼까요?</span>
          </div>
        </div>
      </section>

      <section class="section2">
        <article class="article1">
          <div class="article_left">
            <ul>
              <img src="/images/magazine_detail2_img01.jpg" alt="아껴쓰기이미지">
            </ul>
          </div>
          <div class="article_right">
           <ul>
             <h1 class="mb20"><b>아껴쓰기</b></h1>
             <p>👚 한두 벌의 옷으로 얼마나 다양한 코디가 가능한지 보여주세요!</p>
             <p>가지고 있는 옷을 활용만 해도 충분히 다양하고 예쁜 코디가 가능하다는 것을 보여주세요!</p>
             <p>→ 코트 한벌로 겨울나기</p>
             <p>→ 원피스 한벌로 4가지 코디하기</p>
             <p>→ 같은 바지에 상의만 돌려입기</p>
           </ul>
          </div>
        </article>

        <article class="article2">
          <div class="article_left">
           <ul>
             <img src="/images/magazine_detail2_img02.jpg" alt="나눠쓰기이미지">
           </ul>
          </div>
          <div class="article_right">
            <ul>
              <h1 class="mb20"><b>나눠쓰기</b></h1>
              <p>🧷 새로 만들어진 옷이나 아이템말고, 세월이 묻은 빈티지 아이템,</p>
              <p>퀄리티 좋은 멋스러운 구제 제품을 소개해 보세요!</p>
              <p>→ 내가 알고 있는 빈티지샵 소개하기</p>
              <p>→ 빈티지 아이템 스타일링</p>
              <p>→ 퀼리티좋은 SECOND HAND(구제)고르는 꿀팁!</p>
            </ul>
          </div>
        </article>

        <article class="article3">
          <div class="article_left">
           <ul>
             <img src="/images/magazine_detail2_img03.jpg" alt="바꿔쓰기이미지">
           </ul>
          </div>
          <div class="article_right">
            <ul>
              <h1 class="mb20"><b>바꿔쓰기</b></h1>
              <p>🧣 한 두가지 아이템으로 나만의 다양하고 특별한 연출법을 공유해 주세요!</p>
              <p>→ 목도리 하나로 다양하게 연출하기</p>
              <p>→ 다양한 스카프 연출법!</p>
            </ul>
          </div>

        </article>

        <article class="article4">
          <div class="article_left">
            <ul>
              <img src="/images/magazine_detail2_img04.jpg" alt="다시쓰기이미지">
            </ul>
          </div>
          <div class="article_right">
            <ul>
              <h1 class="mb20"><b>다시쓰기</b></h1>
              <p>👖 나만의 리폼 꿀팁, 이제는 못쓰게 된 옷 활용 방법을 소개해 주세요!</p>
              <p>→ 오래된 원피스 리폼하기!</p>
              <p>→ 청바지 에코백으로 리폼해보기</p>
            </ul>
          </div>
        </article>
      </section>

      <section class="section3">
        <div class="spread-text pt100">
          <p class="mb70">이 밖에 다른 주제도 모두 좋습니다!</p>
          <p>아껴쓰고, 나눠쓰고,</p>
          <p class="mb70">바꿔쓰고, 다시썼던 </p><br>
          <p>나만의 슬로우패션 꿀팁을</p>
          <p>그리니에서 공유해 보세요!</p>
        </div>
        <div class="goChallenge">
          <router-link to="/challenge"><button>→ 챌린지 보러가기</button></router-link>
        </div>
      </section>

    </div><!-- /detail_mag02 -->
  </div>
</template>

<script>
import {gsap} from "gsap/all";
import {ScrollTrigger} from "gsap/ScrollTrigger";


export default {
  name: "Detail2",
  data() {
    return {
      speed: [-9, -3, 5, -7, -5, 2, -9]
    }
  },
  mounted() {
// ball
    gsap.set(".ball", {xPercent: -50, yPercent: -50});
    const ball = document.querySelector(".ball");
    const pos = {x: window.innerWidth / 2, y: window.innerHeight / 2};
    const mouse = {x: pos.x, y: pos.y};
    const mouseSpeed = 0.2;
    const xSet = gsap.quickSetter(ball, "x", "px");
    const ySet = gsap.quickSetter(ball, "y", "px");
    window.addEventListener("mousemove", e => {
      mouse.x = e.x;
      mouse.y = e.y;
    });
    gsap.ticker.add(() => {
      const dt = 1.0 - Math.pow(1.0 - mouseSpeed, gsap.ticker.deltaRatio());
      pos.x += (mouse.x - pos.x) * dt;
      pos.y += (mouse.y - pos.y) * dt;
      xSet(pos.x);
      ySet(pos.y);
    });

// section1
    const bubble = gsap.from('.section1 .bubble', {
      scale: 0,
      opacity: 0,
      paused: true,
      stagger: 0.1, //따로따로 켜지게 설정
    });

    gsap.registerPlugin(ScrollTrigger);
    ScrollTrigger.create({
      trigger: '.section1',
      start: 'top 40%',
      end: 'bottom top',
      onEnter: () => bubble.play(),
    });


    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: '.section1',
        start: 'center center',
        end: 'bottom center',
        scrub: 5,
        pin: true,
        pinSpacing: true,
        toggleActions: 'play pause reverse reset'
      }
    })
    tl.to('.inner', {
      opacity: 0,
      y: '-100%',
      duration: 6,
    })


    const composeTl = gsap.timeline({
      scrollTrigger: {
        trigger: ".active-compose.first",
        start: "center center",
        end: '+=1000',
        scrub: 1,
        pinSpacing: true,
        pin: true,
        toggleActions: "play pause reverse reset",
      }
    });

    composeTl.to(".active-compose.first .text-step1", {
      opacity: 0,
      y: "-100%",
      duration: 6,
    })
      //img1
      .to(".active-compose.first .img-step1", {
        opacity: 1,
        x: "-100%",
        scale: 0.8,
        duration: 6,
      }, "-=6")

      //2
      .to(".active-compose.first .text-step2", {
        opacity: 1,
        y: "0%",
        duration: 6,
      })
      //img2
      .to(".active-compose.first .img-step2", {
        opacity: 1,
        y: "0%",
        duration: 6,
      }, "-=3")

      //3
      .to(".active-compose.first .text-step2", {
        opacity: 0,
        y: "-100%",
        duration: 6,
      }, "+=10")
      .to(".active-compose.first .img-step2", {
        opacity: 1,
        x: "-86%",
        scale: 0.7,
        duration: 6,
      })
      .to(".active-compose.first .img-step1", {
        x: "-150%",
        scale: 0.3,
        duration: 3,
      }, "-=6")
      .to(".active-compose.first .text-step3", {
        opacity: 1,
        y: "0%",
        duration: 6,
      }, "+=3")

      .to(".active-compose.first .img-step3", {
        opacity: 1,
        y: "0%",
        duration: 6,
      }, "-=3");

// secondo anim
    gsap.to(".animated-test", {
      x: 100,
      scrollTrigger: {
        trigger: ".activate-anim",
        start: "top bottom",
        duration: 6,
      }
    })


// section 2
    const ARTICLE = gsap.utils.toArray('article')
    gsap.to(ARTICLE, {
      xPercent: -100 * (ARTICLE.length - 1),
      ease: 'none',
      scrollTrigger: {
        trigger: '.section2',
        end: () => "+=" + document.querySelector('.section2').offsetWidth,
        pin: true,
        scrub: 1,
        snap: 1 / (ARTICLE.length - 1)
      }
    })

// section 3
    gsap.registerPlugin(ScrollTrigger);
    gsap.to('.section3 .spread-text p', {
      backgroundPositionX: '0%',
      stagger: 1,
      scrollTrigger: {
        trigger: '.spread-text',
        scrub: 1,
        start: 'top center',
        end: 'bottom bottom'
      }
    })

  },
  methods: {
    bubbleMove(e) {
      this.speed.forEach((speed, idx) => {
        const x = (window.outerWidth - e.offsetX * speed) / 200;
        const y = (window.outerHeight - e.offsetY * speed) / 200;
        const bubble = document.querySelector(`.bubble-0${idx + 1}`)
        gsap.to(bubble, {x: x, y: y});
      })
    }
  }
}
</script>

<style>
.section3 {
  background: #161616;

}
.spread-text > p {
  background: linear-gradient(
    to right,
    rgb(255, 255, 255) 50%,
    rgb(37, 37, 37) 50%
  );
  background-size: 200% 100%;
  background-position-x: 100%;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  margin-bottom: -250px;
  margin-left: 50px;
  line-height: 1.2;
}

.spread-text p {
  font-size: 100px;
}

.section3 button {
  width: 150px;
  color: #76FF03;
  border: 1px solid #76FF03;
  border-radius: 15px;
  font-weight: 700;
}

.section3 button:hover {
  color: #000;
  background: #76FF03;
  font-weight: 700;
  transition: 1s;
}

.section3 .goChallenge {
  text-align: center;
  padding: 200px 0;

}
</style>
